import React, { useContext } from 'react';
import './SocialMedia.scss';
import { socialMediaLinks } from '../../portfolio';
import StyleContext from '../../contexts/StyleContext';

export default function SocialMedia() {
	const { isDark } = useContext(StyleContext);
	if (!socialMediaLinks.display) {
		return null;
	}
	return (
		<div className="social-media-div">
			{socialMediaLinks.github ? (
				<a
					href={socialMediaLinks.github}
					className="icon-button github"
					target="_blank"
					rel="noopener noreferrer"
				>
					{/* <i className="fab fa-github"></i>
					<i className="fa-brands fa-github"></i> */}
					<span
						className="fa-brands fa-github fa-lg"
						style={{ color: isDark && 'inherit' }}
					></span>
				</a>
			) : null}

			{socialMediaLinks.linkedin ? (
				<a
					href={socialMediaLinks.linkedin}
					className="icon-button linkedin"
					target="_blank"
					rel="noopener noreferrer"
				>
					{/* <i className="fab fa-linkedin-in"></i> */}
					{/* <i className="fa-brands fa-linkedin"></i> */}
					<span
						className="fa-brands fa-linkedin fa-lg"
						style={{ backgroundColor: isDark && 'white' }}
					></span>
				</a>
			) : null}

			{socialMediaLinks.gmail ? (
				<a
					href={`mailto:${socialMediaLinks.gmail}`}
					className="icon-button google"
					target="_blank"
					rel="noopener noreferrer"
				>
					{/* <i className="fas fa-envelope"></i>
					<i class="fa-solid fa-envelope"></i> */}
					<span
						className="fa-solid fa-envelope fa-lg"
						style={{ color: '#EA4335' }}
					></span>
					{/* <span className="fa-duotone fa-envelope"></span> */}
				</a>
			) : null}

			{socialMediaLinks.gitlab ? (
				<a
					href={socialMediaLinks.gitlab}
					className="icon-button gitlab"
					target="_blank"
					rel="noopener noreferrer"
				>
					<i className="fab fa-gitlab"></i>
					<span></span>
				</a>
			) : null}

			{socialMediaLinks.facebook ? (
				<a
					href={socialMediaLinks.facebook}
					className="icon-button facebook"
					target="_blank"
					rel="noopener noreferrer"
				>
					<i className="fab fa-facebook-f"></i>
					<span></span>
				</a>
			) : null}

			{socialMediaLinks.instagram ? (
				<a
					href={socialMediaLinks.instagram}
					className="icon-button instagram"
					target="_blank"
					rel="noopener noreferrer"
				>
					<i className="fab fa-instagram"></i>
					<span></span>
				</a>
			) : null}

			{socialMediaLinks.twitter ? (
				<a
					href={socialMediaLinks.twitter}
					className="icon-button twitter"
					target="_blank"
					rel="noopener noreferrer"
				>
					<i className="fab fa-twitter"></i>
					<span></span>
				</a>
			) : null}

			{socialMediaLinks.medium ? (
				<a
					href={socialMediaLinks.medium}
					className="icon-button medium"
					target="_blank"
					rel="noopener noreferrer"
				>
					{/* <i className="fab fa-medium"></i> */}
					<span
						className="fa-brands fa-medium fa-lg"
						style={{ color: isDark ? 'white' : 'black' }}
					></span>
				</a>
			) : null}

			{socialMediaLinks.stackoverflow ? (
				<a
					href={socialMediaLinks.stackoverflow}
					className="icon-button stack-overflow"
					target="_blank"
					rel="noopener noreferrer"
				>
					<i className="fab fa-stack-overflow"></i>
					<span></span>
				</a>
			) : null}

			{socialMediaLinks.kaggle ? (
				<a
					href={socialMediaLinks.kaggle}
					className="icon-button kaggle"
					target="_blank"
					rel="noopener noreferrer"
				>
					<i className="fab fa-kaggle"></i>
					<span></span>
				</a>
			) : null}
		</div>
	);
}
